/*!
 * Partial: _header.scss
 * Purpose: Header styles for site-wide layout, branding, navigation, and menu toggle.
 * Features: Responsive header, branding, navigation, menu toggle, gradients.
 * Usage: Import in style.scss for global header styles.
 * Dependencies: Uses variables and mixins from abstracts.
 * Last updated: 2025-09-11
 */
@use "../abstracts/variables" as *;
@use "../abstracts/mixins" as *;

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0s !important;
    transition-duration: 0s !important;
  }
}
//Header
.site-header {
  background: $color-text-primary;
  position: relative;
  width: 100vw;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;

  .container {
    @include flex-between;
    margin: 0 auto;
    padding: $padding-md 0; // 12px vertical

    .site-branding {
      a {
        display: block;
        width: min(50vw, 400px);
        height: min(15vw, 77px);
        position: relative;
        overflow: hidden;
        transition: width .2s, height .2s;
        img {
          position: absolute;
          inset: 0;
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }

    nav {
      ul {
        list-style: none;
        @include flex-gap($gap); // gap: 20px
        margin: 0;
        padding: 0;

        a {
          color: $color-text-inverse;
          text-decoration: none;
          font-size: $font-size-base;
          letter-spacing: .0625rem; // ~1px for clarity
          padding: $padding-md $padding-xl; // 12px 20px
          border-radius: $radius;
          position: relative;
          overflow: visible;
          background: none;
          box-shadow: none;
          @include smooth-transition(color, .18s);

          &::after {
            content: '';
            position: absolute;
            left: $padding-xl;
            right: $padding-xl;
            bottom: 8px;
            height: 3px;
            border-radius: 2px;
            @include gradient-bg(90deg, $primary-dark, $primary);
            opacity: 0;
            transform: scaleX(0);
            transition: opacity .18s, transform .22s cubic-bezier(.4,0,.2,1);
            pointer-events: none;
          }

          &:hover::after,
          &:focus-visible::after { opacity: 1; transform: scaleX(1); }

          &:hover,
          &:focus-visible { color: $primary-dark; outline: none; }

          &.active { color: $primary-dark; font-weight: $weight-bold; }
        }
      }
    }
  }

  // Active state when mobile nav open
  .active ul li a { color: $color-text-inverse; background: $brand-primary-light; }

  .menu-toggle {
    display: none;
    position: absolute;
    right: 72px;
    top: 50%;
    transform: translateY(-50%);
    width: 44px;
    height: 44px;
    background: $gray-600;
    border: none;
    border-radius: $radius;
    padding: 0;
    cursor: pointer;
    z-index: 100;
    @include smooth-transition(background, .3s);

    &:hover { background: $gray-500; }
    &:active { transform: translateY(-50%) scale(.95); }

    i {
      font-size: 24px;
      color: $color-text-inverse;
      transition: transform .3s ease, opacity .2s ease;
      display: inline-block;
      &.fa-bars { animation: fadeIn .3s ease; }
      &.fa-xmark { animation: rotateIn .3s ease; }
    }
  }

  .dark-mode-toggle {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    width: 40px;
    height: 40px;
    background: transparent;
    border: 2px solid $gray-300;
    border-radius: 50%;
    padding: 0;
    cursor: pointer;
    z-index: 100;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      border-color: $brand-primary;
      transform: translateY(-50%) scale(1.1);
      box-shadow: 0 4px 12px rgba($brand-primary, 0.3);
    }

    &:focus-visible {
      outline: 2px solid $brand-primary;
      outline-offset: 2px;
    }

    .sun-moon {
      width: 24px;
      height: 24px;
      overflow: visible;

      .sun {
        fill: $gray-600;
        transform-origin: center;
        transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
      }

      .sun-beams {
        stroke: $gray-600;
        stroke-width: 2px;
        stroke-linecap: round;
        transform-origin: center;
        transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.5s cubic-bezier(0.4, 0, 0.2, 1);
      }
    }

    &[aria-checked="false"] {
      .sun {
        fill: $gray-400;
        transform: scale(1.5);
      }

      .sun-beams {
        opacity: 0;
        transform: rotate(-25deg);
        stroke: $gray-400;
      }
    }

    &[aria-checked="true"] {
      background: linear-gradient(135deg, $brand-primary-100, $brand-primary-50);
      border-color: $brand-primary;

      .sun {
        fill: $primary;
        transform: scale(1);
        mask: url(#moon-mask);
      }

      .sun-beams {
        opacity: 1;
        transform: rotate(0deg);
        stroke: $primary;
      }
    }
  }
}
.menu-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.4);
  backdrop-filter: blur(2px);
  z-index: 5;
}

body.no-scroll {
  overflow: hidden;
}


@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes rotateIn {
  from {
    opacity: 0;
    transform: rotate(-90deg) scale(0.8);
  }
  to {
    opacity: 1;
    transform: rotate(0) scale(1);
  }
}

@media (max-width: 900px) {
  .site-header .container {
    flex-direction: row;
    padding: 12px 3vw;
  }

  .site-header .dark-mode-toggle {
    right: 60px;
    width: 36px;
    height: 36px;

    .sun-moon {
      width: 20px;
      height: 20px;
    }
  }

  .site-header nav {
    position: absolute;
    top: 100%;
    right: 0;
    background: $color-text-primary;
    width: 100vw;
    max-width: 320px;
    transform: translateX(100%);
    transition: transform 0.3s, opacity 0.3s, visibility 0.3s;
    box-shadow: -4px 0 20px $overlay-light;
    z-index: 100;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;

    ul {
      flex-direction: column;
      gap: 12px;
      padding: 24px 12px;
      align-items: stretch;

      a {
        text-align: left;
        display: block;
        padding: 12px 14px;
        font-size: $font-size-base;
        color: $color-text-inverse;
        background: none !important;

        &:hover {
          background: $gray-600;
        }

        &.active {
          background: $brand-primary-light;
        }
      }
    }
  }

  .site-header #main-navigation.active {
    transform: translateX(0);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }

  .site-header .menu-toggle {
    display: block;
  }
}

@media (max-width: 600px) {
  .site-header .container {
    flex-direction: row;
    padding: 8px 2vw;
  }

  .site-header nav {
    max-width: 220px;

    ul {
      padding: 12px 6px;
      gap: 7px;

      a {
        padding: 8px 8px;
        font-size: 13px;
        letter-spacing: 0.3px;
      }
    }
  }

  .site-header .menu-toggle {
    width: 40px;
    height: 40px;
    right: 15px;
    padding: 0;

    i {
      font-size: 18px;
    }
  }
}
